<template>
  <v-app>
    <NavigationDrawer :img="sideImageUrl" :drawer="drawer" :mini="mini" />
    <v-app-bar :color="$vuetify.theme.dark ? null : appBarColor" app>
      <v-app-bar-nav-icon @click.stop="drawerHandler()"></v-app-bar-nav-icon>
      <!-- <v-toolbar-title> -->
      <span class="hidden-xs-only">{{ date }}</span>
      <!-- </v-toolbar-title -->

      <v-spacer></v-spacer>
      <v-btn icon @click="theme">
        <img
          v-if="!$vuetify.theme.dark"
          src="/dark_mode_black_24dp.svg"
          alt=""
        />
        <img
          v-if="$vuetify.theme.dark"
          src="/light_mode_white_24dp.svg"
          alt=""
        />
      </v-btn>
      <v-btn icon>
        <img
          v-if="!$vuetify.theme.dark"
          src="/settings_black_24dp.svg"
          alt=""
        />
        <img v-if="$vuetify.theme.dark" src="/settings_white_24dp.svg" alt="" />
      </v-btn>

      <v-btn class="ml-3" x-large icon>
        <img :src="avatarUrl" alt="" />
      </v-btn>
    </v-app-bar>
    <v-main>
      <v-container>
        <router-view />
      </v-container>
    </v-main>
    <v-footer app>hello </v-footer>
  </v-app>
</template>

<script>
import NavigationDrawer from "./components/NavigationDrawer.vue";
import { gregorian_to_jalali } from "./plugins/jalali.js";
export default {
  components: { NavigationDrawer },
  data: () => ({
    drawer: true,
    mini: false,
    avatarUrl:
      "https://avatars.dicebear.com/api/avataaars/admin.svg?m=0&style=circle&top[]=shortHair&topChance=100&eyebrow[]=default&mouth[]=smile&skin[]=light",
    appBarColor: "blue-grey lighten-5",
    sideImageUrl: "http://localhost:8080/sidebar-light.png",
    windowSize: {
      x: 0,
      y: 0,
    },
  }),

  methods: {
    onResize() {
      this.windowSize = { x: window.innerWidth, y: window.innerHeight };
      console.log(window.innerHeight);
    },
    theme() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      this.sideImageUrl = this.$vuetify.theme.dark
        ? "http://localhost:8080/sidebar-dark.png"
        : "http://localhost:8080/sidebar-light.png";
    },
    drawerHandler() {
      this.onResize();
      if (this.windowSize.x <= 1263)
        this.$store.state.drawer = !this.$store.state.drawer;
      if (this.windowSize.x > 1263) this.minimizeHandler();
    },
    minimizeHandler() {
      this.mini = !this.mini;
    },
  },
  computed: {
    date() {
      const now = new Date();
      let weekDay = now.getDay();

      let day = now.getDate();
      let month = now.getMonth() + 1;
      let year = now.getFullYear();
      const shamsi = gregorian_to_jalali(year, month, day);
      const weekDayName = this.$store.state.weekDays[weekDay];
      const shamsiYear = shamsi[0];
      const shamsiMonthName = this.$store.state.monthesName[shamsi[1] - 1];
      const shamsiDay = shamsi[2];
      console.log(shamsi);
      return `${weekDayName} - 
      ${shamsiYear} 
      ${shamsiMonthName}
       ${shamsiDay} `;
    },
  },
  created() {
    this.onResize();
    this.$store.state.drawer = this.windowSize.x <= 1263 ? false : true;
  },
};
</script>
<style>
* {
  font-family: "irs";
}
.theme {
  cursor: pointer;
}
</style>
