<template>
  <v-navigation-drawer
    width="220"
    :src="imageUrl"
    :mini-variant.sync="miniState"
    right
    xs
    v-model="drawerState"
    app
  >
    <v-list>
      <v-list-item justifyContent> hello </v-list-item>
      <v-list-item justifyContent> 
      <router-link to="/">Home</router-link> 
       </v-list-item>
      <v-list-item justifyContent> 
      <router-link to="/about">About</router-link>
       </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
export default {
  name: "NavigationDrawer",
  props: {
    drawer: Boolean,
    mini: Boolean,
    img: String,
  },

  computed: {
    drawerState: {
      get() {
        return this.$store.state.drawer;
      },
      set(state) {
        if (state !== this.$store.state.drawer) {
          this.$store.state.drawer = !this.$store.state.drawer;
        }
      },
    },
    miniState() {
      return this.mini;
    },
    imageUrl() {
      return this.img;
    },
  },
};
</script>

