<template>
  <div>
    <h1>{{ msg }}</h1>
    <div>this is HOme</div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "HelloWorld",
  props: {
    msg: String,
  },
  data() {
    return {
      data: "hi",
    };
  },
  mounted() {
    axios
      .get("http://sandoghmu.test/api/hello")
      .then((res) => (this.data = res.data.hello));
  },
};
</script>

